/* //report styling  */
.dateStyle{
    font-size: smaller;
    font-weight: bold;
    padding: 2px 24px 2px 8px; 
    border: none;
    background-color: lightgrey;
    outline: none;
    appearance: none; /* Remove default arrow */
    -webkit-appearance: none; /* Remove default arrow for WebKit browsers */
    -moz-appearance: none;
    background-image: url('data:image/svg+xml;utf8,<svg fill="%23333" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg"><path d="M7 10l5 5 5-5z"/><path d="M0 0h24v24H0z" fill="none"/></svg>'); /* SVG for the filled arrow icon */
    background-repeat: no-repeat;
    background-position: right 0px center ; /* Adjust the position of the arrow */

}
.descStyle{
    font-size: smaller;
    font-weight: bold;
}
